body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#content {
  display: flex;
  flex-wrap: wrap;
  justify-content:space-between;
}

.card {
  border-style: solid;
  border-width: 2px;
  border-color: black;
  background-color: #dfddb9;
  margin: 1%;
  width: 20%;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}


.Navbar {
    border-style: solid;
    border-width: 2px;
    border-color: black;
    margin: 1%;
}

ul { 
    display: inline-block; 
    list-style-type: none;
}

#switch-name-text:hover {
    cursor: pointer;
    text-decoration: underline;
}

#switch-age-text:hover {
    cursor: pointer;
    text-decoration: underline;
}

#switch-gender-text:hover {
    cursor: pointer;
    text-decoration: underline;
}

